<template>
   <div class="pt-page  animated bounceInLeft " style="
                   "  data-id="blog">
              <div class="section-title-block">
                <h2 class="section-title">Blog</h2>
                <h5 class="section-description">My Diary</h5>
              </div>
              <div class="blog-masonry">
                <!-- Blog Post 1 -->
            <div v-for="datas in data" :key="datas.id.$t" >
                <div class="item">
                  <div class="blog-card">
                    <div class="media-block">
                      <a href="blog-post-1.html">
                        <img class="post-image img-responsive" src="static/images/blog/blog_post_1.jpg" alt="blog-post-1" />
                        <div class="mask"></div>
                        <div class="post-date"><span class="year">2016-02-24</span></div>
                      </a>
                    </div>
                    <div class="post-info">
                      <ul class="category">
                        <li><a href="#">Travel</a></li>
                      </ul>
                      <a href="blog-post-1.html"><h4 class="blog-item-title">{{datas.title.$t}}</h4></a>
                    </div>
                  </div>
                </div>
                </div>
              <div class="loader" v-if="loading">
              </div>
                <p class="loadtext" v-if="loading">LOADING...</p>
                <!-- End of Blog Post 1 -->

              </div>
              </div>
</template>
<script>
export default {
  data () {
    return {
      loading: false,
      data: [],
    }
  },
  created() {
    var self = this;
      self.data = [];
      self.loading = true;
      self.axios.get('https://cors-anywhere.herokuapp.com/http://thinkbaaz.blogspot.com/feeds/posts/default?alt=json'
      ).then((response) => {
      self.data = response.data.feed.entry;
      self.loading = false;
    })

  },
}
</script>
<style>
.loader {
position: fixed;
top: 50%;
margin-top:30px;
left: 50%;
transform: translate(-50%, -50%);
           border-style: solid;
           border-radius: 50%;
           border-color: #e65959;
animation: 1s pulse infinite;
}

.loadtext {
position: fixed;
top: 50%;
margin-top:60px;
left: 47%;
}

@keyframes pulse {
  0% {
height: 0px;
width: 0px;
       border-width: 32px;
opacity: 0;
  }
  40% {
opacity: 1;
  }
  80% {
height: 64px;
width: 64px;
       border-width: 0px;
opacity: 0;
  }
  100%{
opacity: 0;
  }
}
</style>





